.card{
    margin: 5% 2% !important;
}
.infected{
    border-bottom:10px solid rgba(0, 0, 255, 0.5) ;
}
.recovered {
    border-bottom:10px solid rgba(0, 255, 0, 0.5) ;
}
.deaths{
    border-bottom:10px solid rgba(0, 0, 0, 0.5) ;
}

@media(max-width: 770px){
    .card{
        margin:5% 0 !important;
    }
}