body{
    background-color:rgb(250,250,250);
}
.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.image{
    width: 370px;
    margin-top: 50px;
}

@media(max-width: 770px){
    .container{
        margin: 0 10%;
    }
    .image{
        margin:5% 0% !important;
        width: 100%;
    }
}